"use client"
import { useState } from 'react';
import TodoItem from '../components/TodoItem';

export default function Home() {
  const [todos, setTodos] = useState<string[]>([]);
  const [newTodo, setNewTodo] = useState('');

  const handleAddTodo = () => {
    if (newTodo.trim()) {
      setTodos([...todos, newTodo]);
      setNewTodo('');
    }
  };

  return (
    <div className='flex flex-col items-center justify-center min-h-screen bg-gray-100'>
      <h1 className='text-4xl font-bold mb-8'>Todo List</h1>
      <input
className='border p-2 rounded-lg mr-4'
type='text' value={newTodo} onChange={(e) => setNewTodo(e.target.value)} placeholder='Add a new todo...' />
<button className='bg-blue-500 text-white px-4 py-2 rounded-lg' onClick={handleAddTodo}>Add</button>
      <ul className='mt-8'>
        {todos.map((todo, index) => (
          <li key={index} className='flex items-center justify-between p-3 bg-white shadow-md mb-2 rounded-lg'>
            <span>{todo}</span>
<TodoItem todo={todo} />
</li>))}
      </ul>
    </div>
  );
}